$confirmed-color: #448aff;
$death-color: #ff5252;
$recovered-color: #b2ff59;
$active-color: #ffff00;

.App {
  text-align: center;
}

.main-container {
  background-color: #00002d;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  font-size: calc(10px + 2vmin);
  color: #eff5fc;

  .header {
    margin-top: 10px;
    .title {
      font-size: 50px;
      font-weight: bold;
      margin-left: 210px;
    }
    .date {
      float: right;
      font-size: 30px;
      line-height: 66px;
      font-weight: 700;
      margin-right: 30px;
    }
  }
  
  .display-flex {
    display: flex;
  }

  .card-block {
    width: 50%;
    padding: 0 15px;
    margin: 15px 0;
    border-right: 1px solid whitesmoke;
    &.no-boder {
      border: 0;
    }
    .card-container-title {
      margin-bottom: 20px;
      font-size: 40px;
      font-weight: 200;
    }
    .card-container {
      display: flex;
      .cards {
        margin: 0 10px;
        border-bottom: 2px solid whitesmoke;
        border-radius: 6px;
        width: 25%;
        height: 85px;
        background: #11093f;
        .count {
          font-size: 30px;
          font-weight: 600;
          color: white;
        }
        .Confirmed-card {
          color: $confirmed-color;
          font-weight: 500;
        }
      
        .Deaths-card {
          color: $death-color;
          font-weight: 500;
        }
      
        .Recovered-card {
          color: $recovered-color;
          font-weight: 500;
        }
      
        .Active-card {
          color: $active-color;
          font-weight: 500;
        }
        // &:hover {
        //   border: 2px solid whitesmoke;
        // }
      }
    }
  }

  .details-container {
    display: flex;
    .left-panel{
      width: 20%;
      background: #11093f;
      margin-left: 25px;
      border-radius: 6px;
      .search-field {
        width: 80%;
        background: #11093f;
        color: #eff5fc;
        border: 1px solid whitesmoke;
        border-radius: 4px;
        height: 20px;
        padding: 5px;
        font-size: 14px;
        margin: 10px 0;
      }
      .country-list {
        height: 335px;
        overflow-y: auto;
        .country-row {
          font-size: 18px;
          text-align: left;
          border-bottom: 1px solid whitesmoke;
          padding: 12px 24px;
          &:hover {
            color: $confirmed-color;
            cursor: pointer;
          }
        }
      }
    }
    .right-panel {
      width: 80%;
      .chart-container {
        width: 90%;
        margin-left: 40px;
        margin-top: 20px;
      }
    }
  }
  .footer {
    background-color: #00002d;
    color: #448aff;
    line-height: 30px;
    margin-top: 20px;
    .margin-15 {
      margin-left: 15%;
    }
    .margin-left-20 {
      margin-left: 20px;
    }
    .credits {
      text-align: left;
      display: flex;
      font-size: 17px;
      color: #eff5fc;
      margin-left: 25px;
      .made-by {
        font-size: 16px;
        float: right;
        margin-right: 10px;
        color: #448aff;
        i {
          margin-left: 4px;
          color: #448aff;
        }
        .love {
          font-size: 12px;
          color: red;
        }
      }
    }
  }

  @media screen and (max-width: 800px) {
    min-height: 100vh;
    font-size: calc(1vw + 2vmin);

    .header {
      margin-top: 2vw;
      .title {
        font-size: 8vw;
        margin-left: 18vw;
      }
      .date {
        font-size: 3vw;
        line-height: 10vw;
        margin-right: 3vw;
      }
    }
    
    .display-flex {
      display: block;
    }

    .card-block {
      width: 92%;
      margin: 5vw 0;
      border: 0;
      .card-container-title {
        margin-bottom: 4vw;
        font-size: 8vw;
        font-weight: 200;      
      }
      .card-container {
        .cards {
          margin: 0 1vw;
          width: 25%;
          height: 6vh;
          .count {
            font-size: 4vw;
          }
          .Confirmed-card {
            font-size: 3.5vw;
          }
        
          .Deaths-card {
            font-size: 3.5vw;
          }
        
          .Recovered-card {
            font-size: 3.5vw;
          }
        
          .Active-card {
            font-size: 3.5vw;
          }
        }
      }
    }

    .details-container {
      display: block;
      .left-panel{
        width: 90%;
        margin-left: 0 5vw;
        border-radius: 6px;
        .MuiFormControl-root {
          .MuiInputLabel-outlined {
            line-height: 0;
            font-size: 3.5vw;
          }
          .MuiOutlinedInput-input {
            padding: 2.2vw;
          }
        }
        .country-list {
          height: 18vh;
          .country-row {
            font-size: 3.5vw;
            padding: 2vw 5vw;
          }
        }
      }
      .right-panel {
        width: 100%;
        .chart-container {
          width: 95%;
          margin-left: 4vw;
          margin-top: 3vh;
        }
      }
    }
    .footer {
      line-height: 30px;
      .margin-15 {
        margin-left: 0;
      }
      .margin-left-20 {
        margin-left: 0;
      }
      .credits {
        text-align: left;
        display: block;
        font-size: 3vw;
        margin-left: 5vw;
        line-height: 5vw;
        .made-by {
          font-size: 3vw;
          float: right;
          margin-right: 30vw;
          i {
            margin-left: 4px;
          }
          .love {
            font-size: 2vw;
          }
        }
      }
    }
  }
}

